import React from 'react';
import { graphql, Link } from 'gatsby';
import { GatsbySeo } from 'gatsby-plugin-next-seo';
import Img from 'gatsby-image';

import Layout from '../../components/Layout';
import Container from '../../components/Container';
import Footer from '../../components/Footer';
import SpaceDivider from '../../components/SpaceDivider';

import BlockCaseStudyLinks from '../../components/BlockCaseStudyLinks';
import BlockHeaderNoImage from '../../components/BlockHeaderNoImage';
import BlockQuote from '../../components/BlockQuote';
import BlockSteps from '../../components/BlockSteps';

import { colors } from '../../constants';

import { edgesByName } from '../../utils/gql';

export const pageQuery = graphql`
  {
    biggerBuyersImage: file(name: { eq: "bigger-buyers" }) {
      childImageSharp {
        fluid(maxWidth: 520) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    companies: allContentfulCompany {
      edges {
        node {
          name
          image {
            fluid(maxWidth: 120) {
              ...GatsbyContentfulFluid
            }
          }
        }
      }
    }
    hyperikonLedTubes: contentfulAsset(title: { eq: "hyperikon-led-tubes" }) {
      title
      fluid {
        ...GatsbyContentfulFluid
      }
    }
    hyperikonCommercialSales: allContentfulAsset(
      filter: { title: { eq: "hyperikon-commercial-sales" } }
    ) {
      edges {
        node {
          title
          fluid {
            ...GatsbyContentfulFluid
          }
        }
      }
    }
    viceGolfMagazine: allContentfulAsset(
      filter: { title: { eq: "vice-golf-magazine" } }
    ) {
      edges {
        node {
          title
          fluid {
            ...GatsbyContentfulFluid
          }
        }
      }
    }
  }
`;

export default function CaseStudyViceGolf({ data }) {
  const companies = edgesByName(data.companies, node => node.image.fluid);

  return (
    <Layout>
      <GatsbySeo title="Vice Golf case study" description="XXX" />
      <Container className="case-study-page">
        <BlockHeaderNoImage
          pretitle={
            <>
              <Link className="case-study-page-link" to="/case-studies">
                CASE STUDIES
              </Link>
              <span> / VICE GOLF</span>
            </>
          }
          pretitleImage={companies['Hyperikon blue']}
          title="How Vice Golf aggressively stormed the b2b us market"
          desc={
            <span>
              Vice Golf was founded in Germany in 2012 by Ingo Duellmann and
              Rainer Stoeckl. The company started selling the highest-quality
              golf balls first in Germany, expanded into other parts of Europe,
              and entered the U.S. market in 2015.
            </span>
          }
        />

        <Img
          fluid={data.viceGolfMagazine.edges[0].node.fluid}
          imgStyle={{ objectFit: 'cover' }}
          alt="wide"
          className="app__wide-image"
        />

        <BlockSteps
          wide={true}
          title="The brief"
          desc="Vice Golf wanted to grow their B2B sales by targeting golf speciality stores and pro shop businesses. These retail shops expected to pay with net terms, which was a departure from Vice Golf's direct-to-consumer business where consumers had to pay before they could fulfill the order."
          data={[
            {
              text:
                'They wanted a partner who could help them free up working capital from their invoices, so that they could reinvest the funds back into growing the business.',
            },
            {
              text:
                'They wanted a partner who could help them assess the credit risk of their customers, especially since they were a lean team in the US.',
            },
            {
              text:
                'Vice Golf wanted a partner who would make it easy for their customers to pay however they want (check, credit card, wire transfer, ACH, etc).',
            },
            {
              text: 'Wanted a partner who could continue scaling with them.',
            },
          ]}
        />

        <Img
          fluid={data.hyperikonCommercialSales.edges[0].node.fluid}
          imgStyle={{ objectFit: 'cover' }}
          alt="wide"
          className="app__wide-image"
        />

        <BlockSteps
          wide={true}
          title="The solution"
          desc="Despite partnering during COVID, we got off to a great start. We were able to quickly educate their salesforce on how to offer net terms to their sales leads."
          data={[
            {
              title: 'Advance Pay',
              text: `XXXX`,
            },
            {
              title: 'Credit assessments',
              text: 'XXX',
            },
            {
              title: 'Payment Portal',
              text:
                'In just about a few quarters, the company’s wholesale business had rocketed from a small base of buyers to countless business customers and glowing wholesale customer feedback.',
            },
            {
              title: 'API to scale',
              text:
                'In just about a few quarters, the company’s wholesale business had rocketed from a small base of buyers to countless business customers and glowing wholesale customer feedback.',
            },
          ]}
        />

        <BlockQuote
          title="B2B Sales Manager at Vice Golf"
          author="Alex Wicklein"
        >
          "Resolve has been like a spring... allowing us to grow and scale your
          B2B business more quickly than any other company. It's been a catalyst
          to faster and better growth for us."
        </BlockQuote>

        <SpaceDivider height={100} />

        <BlockCaseStudyLinks
          title="More case studies"
          items={[
            {
              title: 'HYPERIKON',
              description:
                'Great Heights reinvented the way conscious couples commit to forever',
              link: '/case-studies/hyperikon',
              image: data.hyperikonLedTubes.fluid,
            },
            {
              title: 'DTC ETHICAL DIAMONDS',
              description:
                'Great Heights reinvented the way conscious couples commit to forever',
              link: '/case-studies/nowvac',
              image: data.biggerBuyersImage.childImageSharp.fluid,
            },
            {
              title: 'DTC ETHICAL DIAMONDS',
              description:
                'Great Heights reinvented the way conscious couples commit to forever',
              link: '/case-studies/nowvac',
              image: data.biggerBuyersImage.childImageSharp.fluid,
            },
          ]}
        />
      </Container>
      <Footer />
      <style jsx global>{`
        .container.case-study-page {
          padding: 0;
          max-width: 100%;
        }

        .case-study-page-link {
          color: ${colors.darkSlateBlue};
        }

        .app__wide-image {
          max-width: 1000px;
          max-height: 400px;
          background: black;
          margin: 0 auto;
        }

        .block-cta .app__title {
          font-size: 42px;
          line-height: 3.5rem;
        }

        .block-cta .app__actions {
          margin-top: 20px;
        }
      `}</style>
    </Layout>
  );
}
