import React from 'react';
import Img from 'gatsby-image';

import { colors } from '../constants';

export default function BlockQuote({ title, author, children, image }) {
  return (
    <section className="block-quote">
      {image && (
        <Img
          fluid={image}
          imgStyle={{ objectFit: 'contain' }}
          alt={title}
          className="block-quote__image"
        />
      )}
      <div className="block-quote__quote">{children}</div>
      <div className="block-quote__author">{author}</div>
      <div className="block-quote__title">{title}</div>

      <style jsx>{`
        .block-quote {
          padding: 60px 0;
          background-color: ${colors.pinkLight};
          position: relative;
          text-align: center;
        }

        .block-quote__quote {
          font-weight: 300;
          font-size: 32px;
          line-height: 1.25;
          max-width: 680px;
          margin: 0 auto;
          padding: 20px 0;
        }

        .block-quote__author {
          font-size: 21px;
          font-weight: 500;
        }

        .block-quote__title {
          font-size: 20px;
        }

        .block-quote:before,
        .block-quote:after {
          top: 0;
          z-index: -1;
          width: 100%;
          height: 100%;
          content: '';
          position: absolute;
          background-color: ${colors.pinkLight};
        }

        .block-quote:before {
          left: -50%;
        }

        .block-quote:after {
          right: -50%;
        }

        .block-quote__quote {
        }
      `}</style>
      <style jsx global>{`
        .block-quote__image {
          max-width: 140px;
          margin: 0 auto;
        }
      `}</style>
    </section>
  );
}
