import React from 'react';
import { navigate } from 'gatsby';
import Img from 'gatsby-image';

import Button from './Button';
import GetDemoButton from './GetDemoButton';

import { screen } from '../constants';

export default function BlockHeaderNoImage({
  title = '',
  desc = '',
  pretitle = '',
  pretitleImage = null,
  pretitleImageAltText = '',
  secondaryButtonText = '',
  secondaryButtonTo = '/',
  primaryButtonText = '',
  primaryButtonTo = '/',
  maxWidth = '800',
}) {
  function handleSecondaryButtonText() {
    navigate(secondaryButtonTo);
  }

  return (
    <section className="block-header-no-image">
      <div className="block-header-no-image__content">
        {pretitleImage && (
          <Img
            fluid={pretitleImage}
            alt={pretitleImageAltText}
            className="app__image block-header-pretitle_image"
          />
        )}
        <div className="block-header-no-image__pretitle app__pretitle">
          {pretitle}
        </div>
        <h1 className="app-hero__title">{title}</h1>
        <p className="app-hero__desc">{desc}</p>
        <div className="app__actions">
          {primaryButtonText && <GetDemoButton text={primaryButtonText} />}
          {secondaryButtonText && (
            <Button large onClick={handleSecondaryButtonText}>
              {secondaryButtonText}
            </Button>
          )}
        </div>
      </div>
      <style jsx>{`
        .block-header-no-image__content {
          text-align: center;
          margin-top: 104px;
          padding-bottom: 50px;
        }
        .block-header-no-image__content .app-hero__desc,
        .block-header-no-image__content .app-hero__title {
          max-width: ${maxWidth}px;
          margin-right: auto;
          margin-left: auto;
        }

        @media (max-width: ${screen.tabletMax}px) {
          .block-header-no-image__content {
            flex-direction: column-reverse;
          }
        }
      `}</style>
      <style jsx global>{`
        .block-header-pretitle_image {
          max-width: 120px;
          margin: 0 auto 20px;
        }
      `}</style>
    </section>
  );
}
