import React from 'react';
import { navigate } from 'gatsby';
import Img from 'gatsby-image';

import { fonts } from '../constants';
import './fonts.css';

export default function CaseStudyCard({ title, description, image, link }) {
  function handleNavigateTo() {
    navigate(link);
  }

  return (
    <div
      role="button"
      tabIndex={0}
      onClick={handleNavigateTo}
      onKeyDown={handleNavigateTo}
      className="case-study-card"
    >
      {image && (
        <Img
          fluid={image}
          imgStyle={{ objectFit: 'cover' }}
          alt={title}
          className="case-study-card__image"
        />
      )}
      <div className="case-study-card__title">{title}</div>
      <div className="case-study-card__description">{description}</div>

      <style jsx>{`
        .case-study-card {
          cursor: pointer;
          transition-duration: 300ms;
        }
        .case-study-card__title {
          font-family: ${fonts.halyardText};
          font-weight: 500;
          padding: 10px 0;
          text-transform: uppercase;
        }
        .case-study-card__description {
          font-family: ${fonts.halyardDisplay};
          font-size: 20px;
          font-weight: 400;
        }
        .case-study-card:hover {
          transform: translateY(-3px);
        }
      `}</style>
      <style jsx global>{`
        .case-study-card__image {
          height: 195px;
        }
      `}</style>
    </div>
  );
}
